import React, { createContext, useContext } from 'react';

// Varsaylan logo URL'si
const defaultLogoUrl = process.env.PUBLIC_URL + '/assets/images/iesyazilim-logo.png';

// Context olutur
const LogoContext = createContext();

// Context hook'u
export const useLogo = () => useContext(LogoContext);

// Logo provider bileeni
export const LogoProvider = ({ children }) => {
  // Gncel logo URL'sini al
  const getLogoUrl = () => {
    return defaultLogoUrl;
  };

  // Context deeri
  const contextValue = {
    getLogoUrl,
    defaultLogoUrl
  };

  return (
    <LogoContext.Provider value={contextValue}>
      {children}
    </LogoContext.Provider>
  );
};

export default LogoContext;